/* make variables */
:root {
  --lightgray: #d9d9d9;
  --gray: #9c9c9c;
  --orange: #f48915;
  --darkGrey: #464d53;
  --caloryCard: #656565;
  --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
  --appColor: #3c3f45;
}

.App {
  background-image: url(data:image/png;base64,);
  mix-blend-mode: overlay;
  background-color: var(--appColor);
  display: flex;
  flex-direction: column;
  gap: 6rem;
  overflow: hidden;
}
::-webkit-scrollbar {
  display: none;
}
* {
  padding: 0;
  margin: 0;
}
a {
  color: inherit;
  text-decoration: none;
}
ul {
  list-style: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: inherit;
}
h2 {
  display: flex;
  font-weight: bold;
  font-size: 3rem;
  text-transform: uppercase;
  font-style: italic;
}
h3 {
  font-weight: bold;
  color: var(--orange);
  text-transform: uppercase;
}
.border-text {
  color: transparent;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
}
.btn {
  background-color: #fff;
  color: #000;
  padding: 0.5rem;
  font-weight: bold;
  border: 2px solid #fff;
  transition: all 0.3s ease-in-out;
  width: 8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
}
.btn:hover {
  cursor: pointer;
  border: 2px solid black;
}
.btn-orange {
  border-color: var(--orange);
  background-color: var(--orange);
  color: #fff;
}
.btn.btn-orange:hover {
  border: 2px solid white;
}

.btn-orange-border {
  background-color: transparent;
  color: #fff;
}
.btn.btn-orange-border:hover {
  border: 2px solid var(--orange);
}

.blur {
  background-color: rgba(253, 120, 43, 0.69);
  position: absolute;
  border-radius: 50%;
  filter: blur(150px);
  z-index: -9;
}
@media screen and (max-width: 1000px) {
  .App {
    gap: 3rem;
  }
  h2 {
    flex-direction: row;
    font-size: x-large;
  }
}
